class WebPlatform {
  constructor() {
    Notification.requestPermission(permission => {
      if (permission === 'denied') {
        alert('Без разрешения на отправку прав TK2 не сможет отправлять уведомления о новых тикетах.');
      }

      console.log('Результат запроса прав:', permission);
    });
  }

  openUrl(url) {
    window.open(url);
  }

  send(channel, ...args) {
    // ipcRenderer.send(channel, ...args);
  }

  openPanelWindow(url, sessionId) {
    window.open(url);
    // ipcRenderer.invoke('open-panel-window', url, sessionId);
  }
}

export default new WebPlatform();
